import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	 components:{
	        wPicker
	    },
	data() {
		return {
			loadStatus:"more",
			pageNum:1,
			pageSize:10,
			total:"",
			list:[],
			searchVal: "",
			dateText: "按日期筛选",
			startDate:"",
			endDate:"",
			starArr: [
				"全部评分",
				"5星",
				"4星",
				"3星",
				"2星",
				"1星"
			],
			star: "按评分筛选",
			evaluateScore:""
		}
	},
	onLoad() {
		this.clearList();
		this.getList(1);
	},
	
	methods: {
		reload(){
			this.clearList();
			this.getList(1);
		},
		getList(startPage){
			const that = this;
			let param = {
				dynamicParam:that.searchVal,
				startPage: startPage,
				pageSize: that.pageSize,
				dateStart:that.startDate,
				dateEnd:that.endDate,
				evaluateScore:that.evaluateScore
			}
			that.loadStatus="loading";
			that.$api.dailyList(param).then(res => {
				if (res.success) {
					let result = res.datas.rows;
					that.total= res.datas.total;
					for(var i in result){
						result[i].headImgUrl = result[i].headImgUrl?result[i].headImgUrl:'../../../../static/images/defaultUserPic.png';
						that.list.push(result[i]);
					}
					let total = res.datas.total;
					let totalPageNum =Math.ceil(total/that.pageSize);
					if(parseInt(totalPageNum)>parseInt(that.pageNum)){
						that.pageNum++;
						that.loadStatus="more";
					}
					else{
						that.loadStatus="noMore";
					}
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		clearList(){
			const that = this;
			that.pageNum=1;
			that.total="";
			that.list=[];
		},
		search() {
			const that = this;
			that.clearList();
			that.getList(1);
		},
		searchCancel() {
			const that = this;
			that.searchVal = "";
			that.clearList();
			that.getList(1);
			uni.hideKeyboard();
		},
		datePicker: function(e) {
			const that = this;
			that.$refs.range.show();
		},
		onConfirm(val){
			const that= this;
			that.dateText=val.result;
			that.startDate=val.obj.fyear+'-'+val.obj.fmonth+'-'+val.obj.fday+' 00:00:00';
			that.endDate=val.obj.tyear+'-'+val.obj.tmonth+'-'+val.obj.tday+' 23:59:59';
			that.clearList();
			that.getList(1);
		},
		clearDate(){
			const that = this;
			that.startDate="";
			that.endDate="";
			that.dateText="按日期筛选";
			that.clearList();
			that.getList(1);
		},
		starPicker: function(e) {
			const that = this;
			let index = e.detail.value;
			that.star = that.starArr[index];
			if(index==0){
				that.evaluateScore="";
			}
			else{
				that.evaluateScore=6-index;
			}
			that.clearList();
			that.getList(1);
		},
		gotoDetail(id){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/journal/detail",
				query:{
					id:id
				}
			})
		},
		gotoAdd(){
			const that =this;
			that.$Router.push({
				path:"/pages/my/manageNavs/journal/add"
			})
		},
		gotoProcess(id){
			const that =this;
			that.$Router.push({
				path:"/pages/my/manageNavs/process/index",
				query:{
					id:id
				}
			})
		}
	},
	onReachBottom() {
		const that = this;
		if(that.loadStatus=="noMore"){
			return;
		}
		else if(that.pageNum!=1){
			that.getList(that.pageNum);
		}
	},
}
